import React from 'react'
import styled, { css } from 'styled-components'
import {
  SlideDefaultWrap,
  SlideDefaultInner,
  SlideDefaultImg,
} from './SlideDefault'

const SlideParallaxLayer = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
`

type PropType = React.HTMLAttributes<HTMLDivElement> & {
  width: number
  imgSrc: string
  parallax: number
}

export const SlideParallax = (props: PropType) => {
  const { width, imgSrc, parallax, ...restProps } = props

  return (
    <SlideDefaultWrap
      {...restProps}
      css={css`
        min-width: ${width}%;
      `}
    >
      <SlideDefaultInner>
        <SlideParallaxLayer style={{ transform: `translateX(${parallax}%)` }}>
          <SlideDefaultImg src={imgSrc} />
        </SlideParallaxLayer>
      </SlideDefaultInner>
    </SlideDefaultWrap>
  )
}
