import React, { PropsWithChildren } from 'react'
import styled, { css } from 'styled-components'
import { H2Style } from '../../Text'
import { media, transition } from '../../Settings'
import { Button } from '../../Button'

const SlideThumbnailWrap = styled.div`
  ${H2Style};
  height: 100%;
  position: relative;
  padding-left: 0.3rem;

  ${media.minSm} {
    padding-left: 0.6rem;
  }
  ${media.minMd} {
    padding-left: 0.8rem;
  }
`

const SlideThumbnailInner = styled(Button)`
  display: block;
  height: 100%;
  width: 100%;
  background-position: center;
  background-size: cover;
  transition: opacity ${transition.default};
`

type PropType = React.HTMLAttributes<HTMLDivElement> & {
  width: number
  imgSrc: string
  selected: boolean
  onClick: () => void
}

export const SlideThumbnail = (props: PropsWithChildren<PropType>) => {
  const { width, imgSrc, selected, onClick, ...restProps } = props

  return (
    <SlideThumbnailWrap
      {...restProps}
      css={css`
        min-width: ${width}%;
      `}
    >
      <SlideThumbnailInner
        onClick={onClick}
        tabAccessType="inner"
        css={css`
          opacity: ${selected ? 1 : 0.2};
          background-image: url(${imgSrc});
        `}
      />
    </SlideThumbnailWrap>
  )
}
