import React from 'react'
import styled, { css } from 'styled-components'
import { media } from '../../Settings'

export const SlideDefaultWrap = styled.div`
  position: relative;
  height: 100%;
  padding-left: 0.6rem;
  padding-top: 0.6rem;

  ${media.minSm} {
    padding-left: 0.8rem;
    padding-top: 0.8rem;
  }
  ${media.minMd} {
    padding-left: 1rem;
    padding-top: 1rem;
  }
`

export const SlideDefaultInner = styled.div`
  position: relative;
  height: 100%;
  overflow: hidden;
`

export const SlideDefaultImg = styled.img`
  position: absolute;
  display: block;
  top: 50%;
  left: 50%;
  width: auto;
  min-height: 100%;
  min-width: 100%;
  max-width: none;
  transform: translate(-50%, -50%);
`

type PropType = React.HTMLAttributes<HTMLDivElement> & {
  width: number
  imgSrc: string
}

export const SlideDefault = (props: PropType) => {
  const { width, imgSrc, ...restProps } = props

  return (
    <SlideDefaultWrap
      {...restProps}
      css={css`
        min-width: ${width}%;
      `}
    >
      <SlideDefaultInner>
        <SlideDefaultImg src={imgSrc} />
      </SlideDefaultInner>
    </SlideDefaultWrap>
  )
}
